import React from "react";
import Pageheader from "./Pageheader";
import me from "../me1.jpg";
const About = () => {
    return (
        <div className="about-wraper" id="about">
            <Pageheader title={'About Me'}/> 
            <div className="about-container">
                <div className="about-text">
                    <h2>Hello! I'm Sameer</h2>
                    <p>
                        I'm a Software developer based in the mumbai Maharashtra.I don’t like to define myself by the work I’ve done. 
                        I define myself by the work I want to do. Skills can be taught, personality is inherent. 
                        I prefer to keep learning, continue challenging myself, and do interesting things that matter.
                    </p>
                    <p>
                        Fueled by high energy levels and boundless enthusiasm, I’m easily inspired and more then willing to follow my fascinations wherever they take me.
                        I’m passionate, expressive, multi-talented spirit with a natural ability to entertain and inspire.
                        I’m never satisfied to just come up with ideas. Instead I have an almost impulsive need to act on them.
                    </p>
                </div>
                <div className="about-photo">
                    <img className ="about-me" src = {me} alt ="Self Image"></img>
                </div>
            </div>
        </div>
    )
}

export default About
