import React from "react";
import logo from "../logo2.png";
//react font awsome
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {faBars} from "@fortawesome/free-solid-svg-icons";

const Navbar = () => {
    return (
        <nav className="navbar fixed-top scrolling-navbar navbar-expand-lg navbar-light bg-dark">
        <div className="container">
        <a className="navbar-brand" href="#"><img className="logo" src={logo} alt= "Logo..."></img></a>
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <FontAwesomeIcon icon={faBars} style={{color:"#fff"}}/>
        </button>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav ml-auto">
            <li className="nav-item active">
                <a className="nav-link" href="#header">Home <span className="sr-only">(current)</span></a>
            </li>
            <li className="nav-item">
                <a className="nav-link" href="#about">About Me</a>
            </li>
            <li className="nav-item">
                <a className="nav-link" href="#services">Services</a>
            </li>
            <li className="nav-item">
                <a className="nav-link" href="#experience">Experiences</a>
            </li>
            <li className="nav-item">
                <a className="nav-link" href="#contactus">Contacts</a>
            </li>
            </ul>
        </div>
        </div>
        </nav>
    )
}

export default Navbar
