import React from "react";
import Pageheader from "./Pageheader";
import logo1 from "../laptop-screen.png";
import logo2 from "../code.png";
import logo3 from "../seo.png";
const Services = () => {
    return (
        <div className="service-wraper" id="services">
            <Pageheader title={'My Services'}/>
            <div className="service-container">
                    <div className="card">
                        <img className="card-img-top" src={logo1} alt="Card image cap"></img>
                        <div className="card-body">
                            <h3 className="service-text">Web Design</h3>
                            <p className="card-text">I approach each project individually and always focus on the result.</p>
                        </div>
                    </div>
                <div className="card">
                    <img className="card-img-top" src={logo2} alt="Card image cap"></img>
                    <div className="card-body">
                        <h3 className="service-text2">Web Development</h3>
                        <p className="card-text">Your website will be build with an new proven technologies.</p>
                    </div>
                </div>
                <div className="card">
                    <img className="card-img-top" src={logo3} alt="Card image cap"></img>
                    <div className="card-body">
                        <h3 className="service-text">Google Ads</h3>
                        <p className="card-text">Your services or product will appear at the top of the Google Search.</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Services
