import React from "react";
import { FaInstagramSquare, FaGithub, FaLinkedin } from "react-icons/fa";

const handleURL = (url) =>{
    return () => window.open(url, "_blank")
}

const Contact = () => {
    return (
        <div className="contact-wraper" id="contact">
            <div className="ContactIcons">
                <FaInstagramSquare color='white' size='30px' style={{padding:"1px"}} 
                onClick={handleURL('https://www.instagram.com/jhasameer_/')} />
                <FaGithub color='white' size='30px' style={{padding:'1px'}}
                onClick={handleURL('https://github.com/sameerjha18')} />
                <FaLinkedin color='white' size='30px' style={{padding:'1px'}}
                onClick={handleURL('https://www.linkedin.com/in/sameer-jha-820445190/')} />
            </div>
            <div className="contact-text">
                <span>
                   Copyright &copy; 2021 Sameer jha. All rights reserved.
                </span>
            </div>
        </div>
    )
}

export default Contact
